.auth-form {
	height: 100vh;
	margin: 0 auto;
	max-width: 396px;
	background-color: inherit;
	display: flex;
	flex-direction: column;
	color: white;
}

.auth-form__container {
	display: flex;
	flex-direction: column;
	height: 100%;
	justify-content: space-between;
	padding: 70px 0;
}

.auth-form__form {
	display: flex;
	flex-direction: column;
	gap: 10px;
	width: 100%;
	padding: 40px 0 0;
}

.auth-form__button {
	font-size: 14px;
	font-weight: 500;
	text-align: center;
	min-height: 45px;
	border: 0;
	background-color: #f1eb53;
	cursor: pointer;
	border-radius: 2px;
	width: 100%;
}

.auth-form__button_inactive {
	background-color: #A0A0A0;
	cursor: default;
}

.auth-form__button:not(:disabled):hover {
	opacity: 0.8;
}

.auth-form__link {
	color: #FF6838;
	cursor: pointer;
	text-decoration: none;
}

.auth-form__link:hover {
	opacity: 0.6;
}

.auth-form__question {
	margin: 0;
	padding-top: 16px;
	text-align: center;
	color: #A0A0A0;
	font-size: 14px;
}

.auth-form__title {
	padding-top: 40px;
	font-size: 24px;
	font-weight: 500;
	margin: 0;
}

@media screen and (max-width: 768px) {
	.auth-form__container {
		padding: 232px 0;
	}
}

@media screen and (max-width: 425px) {
	.auth-form__container {
		padding: 56px 0 30px;
	}

	.auth-form {
		max-width: 260px;
		display: flex;
		align-items: center;
	}

	.auth-form__button, 
	.auth-form__question,
	.auth-form__link {
		font-size: 12px;
	}

	.auth-form__question {
		padding-top: 14px;
	}

	.auth-form__header {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}